import React from 'react'
import { Link } from 'react-router-dom'
import mag from './images/img3.png'

function Card(props) {
    return (
        <div>
              
                <div class="card cardpic" >
                    <img src={mag} class="card-img-top" alt="..." />
                    <div class="card-body">
                        <h5 class="card-title">{props.title}</h5>
                        <p class="card-text">{props.desc}</p>
                        <button type="button" class="btn btn-outline-primary my-3">
                            <Link to="/carddesc" className="nav-link exp" aria-current="page">Explore Now!</Link>
                        </button>
                    </div>
                </div>
               
        </div>
    )
}

export default Card
