import React from 'react'

function Contact() {
    return (
        <div className="my-4">
            <h1 className="text-center">Contact Us</h1>
            <div className="cont contact">
                <div class="row mb-3">
                    <div class="col">
                        <input type="text" class="form-control" placeholder="First name" aria-label="First name" />
                    </div>
                    <div class="col">
                        <input type="text" class="form-control" placeholder="Last name" aria-label="Last name" />
                    </div>
                </div>
                <div class="mb-3">
                    {/* <label for="exampleFormControlInput1" class="form-label">Mobile</label> */}
                    <input type="number" class="form-control" id="exampleFormControlInput1" placeholder="Your mobile number here" />
                </div>
                <div class="mb-3">
                    {/* <label for="exampleFormControlInput1" class="form-label">Email</label> */}
                    <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="Your Email Here" />
                </div>
                <div class="mb-3">
                    {/* <label for="exampleFormControlTextarea1" class="form-label">Your MEssage Here</label> */}
                    <textarea class="form-control" id="exampleFormControlTextarea1" placeholder="Enter your message" rows="3"></textarea>
                </div>
                <div class="col-12">
                    <button class="btn btn-primary" type="submit">Submit form</button>
                </div>
            </div>
        </div>
    )
}

export default Contact
